"use client";

import React, { useState } from "react";
import { Plus, Minus, Calendar, MapPin, Users } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

export const LumaCalendar = () => {
  const [isOpen, setIsOpen] = useState(false);

  // Sample events data
  const events = [
    {
      id: 1,
      title: "First Order Founders Meetup",
      date: "Aug 15, 2025",
      time: "6:00 PM",
      location: "Brooklyn, NY",
      attendees: 25,
      description: "Monthly meetup for early-stage founders to discuss challenges and share insights."
    },
    {
      id: 2,
      title: "Product Strategy Workshop",
      date: "Aug 22, 2025",
      time: "2:00 PM",
      location: "Manhattan, NY",
      attendees: 15,
      description: "Deep dive into product strategy fundamentals for technical founders."
    },
    {
      id: 3,
      title: "First Principles Thinking Session",
      date: "Aug 29, 2025",
      time: "7:00 PM",
      location: "Queens, NY",
      attendees: 30,
      description: "Interactive workshop on applying first principles thinking to startup challenges."
    }
  ];

  return (
    <>
      {/* Compact View */}
      <div className="flex items-center justify-between p-6 h-40 bg-gradient-to-br from-blue-50 to-indigo-50 border border-blue-200 rounded-lg shadow-sm hover:shadow-md hover:border-blue-300 transition-all duration-300 ease-in-out hover:-translate-y-1 group">
        <div className="flex flex-col justify-center h-full">
          <div className="flex items-center mb-2">
            <Calendar className="h-4 w-4 text-blue-600 mr-2" />
            <h3 className="font-bold text-black tracking-wider uppercase text-sm group-hover:text-blue-800 transition-colors duration-300">
              NYC EVENTS
            </h3>
          </div>
          <p className="text-black text-sm leading-relaxed max-w-sm group-hover:text-blue-700 transition-colors duration-300">
            Join our community events happening across NYC. From founder meetups to strategy sessions.
          </p>
          <div className="mt-2 text-xs text-blue-600 font-medium">
            Next: {events[0].title} • {events[0].date}
          </div>
        </div>
        <button
          onClick={() => setIsOpen(true)}
          className="p-3 hover:bg-blue-100 rounded-full transition-all duration-200 cursor-pointer hover:scale-110 group-hover:bg-blue-50"
        >
          <Plus className="h-5 w-5 text-blue-600 group-hover:text-blue-700 transition-colors duration-300" />
        </button>
      </div>

      {/* Modal Overlay */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center"
            onClick={() => setIsOpen(false)}
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              transition={{ duration: 0.2 }}
              className="relative bg-white rounded-lg max-w-3xl w-full mx-4 max-h-[80vh] overflow-hidden"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="overflow-y-auto max-h-[80vh]">
                <div className="p-8 space-y-6">
                  <div className="flex items-center justify-between">
                    <div>
                      <h2 className="font-bold text-black text-3xl tracking-[-0.02em] mb-2">
                        NYC Events Calendar
                      </h2>
                      <p className="text-gray-600 text-sm">
                        Powered by Luma • Connect with fellow founders
                      </p>
                    </div>
                    <button
                      onClick={() => setIsOpen(false)}
                      className="p-2 hover:bg-gray-100 rounded-full transition-all duration-200"
                    >
                      <Minus className="h-4 w-4 text-black" />
                    </button>
                  </div>

                  <div className="space-y-4">
                    {events.map((event) => (
                      <div key={event.id} className="border border-gray-200 rounded-lg p-6 hover:border-blue-300 hover:bg-blue-50/30 transition-all duration-200">
                        <div className="flex justify-between items-start mb-3">
                          <h3 className="font-semibold text-lg text-black">
                            {event.title}
                          </h3>
                          <div className="flex items-center text-sm text-gray-500">
                            <Users className="h-4 w-4 mr-1" />
                            {event.attendees} attending
                          </div>
                        </div>
                        
                        <div className="flex items-center space-x-4 text-sm text-gray-600 mb-3">
                          <div className="flex items-center">
                            <Calendar className="h-4 w-4 mr-1" />
                            {event.date} at {event.time}
                          </div>
                          <div className="flex items-center">
                            <MapPin className="h-4 w-4 mr-1" />
                            {event.location}
                          </div>
                        </div>
                        
                        <p className="text-gray-700 text-sm leading-relaxed mb-4">
                          {event.description}
                        </p>
                        
                        <button className="px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700 transition-colors duration-200">
                          View on Luma
                        </button>
                      </div>
                    ))}
                  </div>

                  <div className="pt-4 border-t border-gray-200">
                    <div className="flex items-center justify-between">
                      <p className="text-sm text-gray-600">
                        Want to host an event? Get in touch.
                      </p>
                      <button className="px-4 py-2 border border-gray-300 text-gray-700 text-sm font-medium rounded-lg hover:bg-gray-50 transition-colors duration-200">
                        Contact Us
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
};

export default LumaCalendar;
